import React, { PureComponent, Fragment } from 'react';
import { Select, Checkbox, FormControl } from '../src';
import { FormControlType } from '../src/FormControl/FormControlType';
import { SizeType } from '../src/common';

export default class FormControlDocs extends PureComponent {
  state = {
    type: FormControlType.RADIO,
    name: 'formControlDocsInputName',
    id: 'formControlDocsInputId',
    placeholder: 'FormControl element',
    step: 1,
    locale: 'en',
    options: [
      { value: '1', label: 'One' },
      { value: '2', label: 'Two' },
      { value: '3', label: 'Three', secondary: 'This is a secondary text' },
    ],
    disabled: false,
    readOnly: false,
    required: false,
    autoComplete: false,
    /* eslint-disable no-console */
    onChange: value => {
      console.log('[FormControl] onChange called');
      this.setState({ value });
    },
    onBlur: () => console.log('[FormControl] onBlur called'),
    onFocus: () => console.log('[FormControl] onFocus called'),
    /* eslint-enable no-console */
    minLength: undefined,
    maxLength: undefined,
    min: 3,
    max: 12,
    value: '',
    searchPlaceholder: 'Search placeholder',
    size: SizeType.MD,
    types: [
      { value: FormControlType.TEXT, label: 'Text' },
      { value: FormControlType.TEXTAREA, label: 'Textarea' },
      { value: FormControlType.PASSWORD, label: 'Password' },
      { value: FormControlType.NUMBER, label: 'Number' },
      { value: FormControlType.SELECT, label: 'Select' },
      { value: FormControlType.RADIO, label: 'Radio' },
      { value: FormControlType.CHECKBOX, label: 'Checkbox' },
      // { value: FormControlType.UPLOAD, label: 'Upload' },
      { value: FormControlType.DATE, label: 'Date' },
      { value: FormControlType.TEL, label: 'Telephone' },
    ],
  };

  getComponentCode() {
    if ([FormControlType.SELECT, FormControlType.RADIO].includes(this.state.type)) {
      return `<FormControl
  type="${this.state.type}"
  label="${this.state.label}"
  helpText="${this.state.helpText}"
  name="${this.state.name}"
  disabled="${this.state.disabled}"
  options="${JSON.stringify(this.state.options)}"
  onChange={this.handleOnChange}
  readOnly="${this.state.readOnly}"
  required="${this.state.required}"
  min="${this.state.min}"
  max="${this.state.max}"
/>`;
    }
    return `<FormControl
  type="${this.state.type}"
  label="${this.state.label}"
  helpText="${this.state.helpText}"
  name="${this.state.name}"
  disabled="${this.state.disabled}"
  onChange={this.handleOnchange}
  readOnly="${this.state.readOnly}"
  required="${this.state.required}"
  min="${this.state.min}"
  max="${this.state.max}"
/>`;
  }

  render() {
    const {
      type,
      name,
      id,
      placeholder,
      step,
      locale,
      options,
      disabled,
      readOnly,
      required,
      autoComplete,
      onChange,
      onBlur,
      onFocus,
      minLength,
      maxLength,
      min,
      max,
      value,
      searchPlaceholder,
      size,
    } = this.state;
    return (
      <div className="container">
        <section className="section">
          <div className="row m-t-4">
            <div className="col-md-6">
              <h2>FormControl</h2>
              <p>To render atomic form elements driven by Dynamic Forms API schema</p>
            </div>
            <div className="col-md-6">
              <FormControl
                type={type}
                name={name}
                id={id}
                placeholder={placeholder}
                step={step}
                locale={locale}
                options={options}
                disabled={disabled}
                readOnly={readOnly}
                required={required}
                autoComplete={autoComplete}
                onChange={onChange}
                onBlur={onBlur}
                onFocus={onFocus}
                minLength={minLength}
                maxLength={maxLength}
                min={min}
                max={max}
                value={value}
                searchPlaceholder={searchPlaceholder}
                size={size}
              />
              <div className="m-t-2">
                {this.state.value && <pre>{JSON.stringify(this.state.value)}</pre>}
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-md-6">
              <pre className="tw-docs-code">{this.getComponentCode()}</pre>
            </div>

            <div className="col-md-6 m-t-2">
              <Select
                selected={this.state.types.find(t => t.value === this.state.type)}
                options={this.state.types}
                onChange={option => {
                  this.setState({ type: option.value });
                }}
              />

              {[FormControlType.TEXT, FormControlType.TEXTAREA, FormControlType.NUMBER].includes(
                type,
              ) && (
                <div className="m-t-2 form-group">
                  <label htmlFor="for">Value</label>
                  <input
                    type="text"
                    id="for"
                    className="form-control"
                    value={value}
                    onChange={({ target: { value: newValue } }) =>
                      this.setState({ value: newValue })
                    }
                  />
                </div>
              )}

              <div className="m-t-2">
                <Checkbox
                  checked={this.state.disabled}
                  label="Is control disabled?"
                  onChange={() => {
                    this.setState({ disabled: !this.state.disabled });
                  }}
                />

                <Checkbox
                  checked={this.state.readOnly}
                  label="Is control readonly?"
                  onChange={() => {
                    this.setState({ readOnly: !this.state.readOnly });
                  }}
                />

                <Checkbox
                  checked={this.state.required}
                  label="Is control required?"
                  onChange={() => {
                    this.setState({ required: !this.state.required });
                  }}
                />
              </div>

              {this.state.type === FormControlType.NUMBER && (
                <Fragment>
                  <div className="m-t-2">
                    <label htmlFor="numberMin">Min</label>
                    <input
                      name="numberMin"
                      className="form-control"
                      type="number"
                      value={this.state.min}
                      onChange={event => this.setState({ min: event.target.value })}
                    />
                  </div>
                  <div className="m-t-2">
                    <label htmlFor="numberMax">Max</label>
                    <input
                      name="numberMax"
                      className="form-control"
                      type="number"
                      value={this.state.max}
                      onChange={event => this.setState({ max: event.target.value })}
                    />
                  </div>
                  <div className="m-t-2">
                    <label htmlFor="numberStep">Step</label>
                    <input
                      name="numberStep"
                      className="form-control"
                      type="number"
                      value={this.state.step}
                      onChange={event => this.setState({ step: event.target.value })}
                    />
                  </div>
                </Fragment>
              )}
            </div>
          </div>
        </section>
      </div>
    );
  }
}



