import React, { PureComponent, Fragment } from 'react';
import Types from 'prop-types';
import { FormControlType } from './FormControlType';
import Radio from '../radio';
import Select from '../select';
import DateInput from '../dateInput/DateInput';
import PhoneNumberInput from '../phoneNumberInput/PhoneNumberInput';
import Checkbox from '../checkbox/Checkbox';
import { SizeType, uniqueId } from '../common';

import './FormControl.less';

export default class FormControl extends PureComponent {
  static propTypes = {
    type: Types.oneOf(Object.values(FormControlType)),
    name: Types.string.isRequired,
    id: Types.string,
    placeholder: Types.string,
    step: Types.number,
    locale: Types.string,
    // uploadAccept: Types.bool,
    // uploadIcon: Types.string,
    // uploadTooLargeMessage: Types.string,
    // helpImage: Types.string,
    options: Types.arrayOf(
      Types.shape({
        label: Types.string.isRequired,
        value: Types.oneOfType([Types.number, Types.string, Types.bool, Types.object]).isRequired,
        secondary: Types.string,
      }),
    ),
    disabled: Types.bool,
    readOnly: Types.bool,
    required: Types.bool,
    autoComplete: Types.bool,
    onChange: Types.func.isRequired,
    onBlur: Types.func,
    onFocus: Types.func,
    minLength: Types.number,
    maxLength: Types.number,
    min: Types.number,
    max: Types.number,
    // pattern: Types.instanceOf(RegExp),
    value: Types.oneOfType([Types.string, Types.number, Types.bool, Types.object]),
    // uploadOptions: Types.shape({}),
    // textFormat: Types.string,
    // validationAsync: Types.func
    searchPlaceholder: Types.string,
    size: Types.oneOf(Object.values(SizeType)),
  };

  static defaultProps = {
    type: FormControlType.TEXT,
    id: '',
    placeholder: null,
    locale: null,
    options: [],
    step: 1,
    // uploadAccept: false,
    // uploadIcon: null,
    // uploadTooLargeMessage: null,
    // helpImage: null,
    disabled: false,
    readOnly: false,
    required: true,
    autoComplete: true,
    onBlur() {},
    onFocus() {},
    min: null,
    max: null,
    minLength: null,
    maxLength: null,
    value: undefined,
    searchPlaceholder: null,
    size: SizeType.MD,
  };

  state = {
    id: undefined,
    selectedOption: undefined,
    value: this.props.value,
    touched: false,
  };

  componentDidMount() {
    const { id } = this.props;
    if (!id) {
      // eslint-disable-next-line react/no-did-mount-set-state
      this.setState({
        id: uniqueId(),
      });
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.value !== prevProps.value) {
      // eslint-disable-next-line react/no-did-update-set-state
      this.setState({ value: this.props.value });
    }
  }

  /**
   * autocomplete hides our form help so we need to disable it when help text
   * is present. Chrome ignores autocomplete=off, the only way to disable it is
   * to provide an 'invalid' value, for which 'disabled' serves.
   */
  getAutocompleteStatus = () => (this.props.autoComplete ? 'on' : 'disabled');

  setControlTouched = () => {
    if (!this.state.touched) {
      this.setState({ touched: true });
    }
  };

  handleOnChange = event => {
    let value;

    this.setControlTouched();

    if (typeof event === 'object') {
      if (event.target && event.target.value) {
        // This is a SyntheticEvent coming from React
        ({ value } = event.target);
      } else if (event.value) {
        // If we don't have a target but the emitted event
        // has a value it's coming from our Select or Radio
        // components
        ({ value } = event);
      } else {
        // In any other case we just emit the event as it is
        value = event;
      }
    } else {
      // This is coming from our Checkbox component which is
      // a boolean basically, so we must emit that value
      value = event;
    }

    this.setState({ value });
    this.props.onChange(value);
  };

  handleOnFocus = event => this.props.onFocus(event);

  handleOnBlur = event => {
    this.setControlTouched();
    this.props.onBlur(event);
  };

  render() {
    const id = this.props.id || this.state.id;
    const {
      name,
      placeholder,
      step,
      locale,
      type,
      options,
      disabled,
      readOnly,
      required,
      minLength,
      maxLength,
      min,
      max,
      searchPlaceholder,
      size,
    } = this.props;
    const { value } = this.state;
    switch (type) {
      case FormControlType.RADIO: {
        return (
          <Fragment>
            {options.map((option, index) => (
              // eslint-disable-next-line react/no-array-index-key
              <Fragment key={index}>
                <Radio
                  label={option.label}
                  id={`${index}`}
                  name={name}
                  secondary={option.secondary}
                  onFocus={this.handleOnFocus}
                  onChange={() => {
                    this.setState({ selectedOption: option });
                    this.handleOnChange(option);
                  }}
                  onBlur={this.handleOnBlur}
                  checked={
                    this.state.selectedOption && this.state.selectedOption.value === option.value
                  }
                  disabled={disabled}
                />
              </Fragment>
            ))}
          </Fragment>
        );
      }
      case FormControlType.CHECKBOX: {
        return (
          <Checkbox
            checked={value}
            required={required}
            disabled={disabled}
            label={placeholder}
            onFocus={this.handleOnFocus}
            onChange={this.handleOnChange}
            onBlur={this.handleOnBlur}
          />
        );
      }
      case FormControlType.SELECT: {
        return (
          <Select
            id={id}
            selected={
              this.state.selectedOption &&
              options.find(option => this.state.selectedOption.value === option.value)
            }
            options={options}
            onChange={newValue => {
              this.setState({ selectedOption: newValue });
              this.handleOnChange(newValue);
            }}
            onFocus={this.handleOnFocus}
            onBlur={this.handleOnBlur}
            required={required}
            disabled={disabled}
            placeholder={placeholder}
            searchPlaceholder={searchPlaceholder}
          />
        );
      }
      case FormControlType.NUMBER: {
        return (
          <input
            type="number"
            className="form-control"
            id={id}
            step={step}
            placeholder={placeholder}
            value={value}
            readOnly={readOnly}
            required={required}
            disabled={disabled}
            min={min}
            max={max}
            onChange={this.handleOnChange}
            onBlur={this.handleOnBlur}
            onFocus={this.handleOnFocus}
            autoComplete={this.getAutocompleteStatus()}
          />
        );
      }
      case FormControlType.HIDDEN: {
        return <input type="hidden" name={name} value={value} />;
      }
      case FormControlType.PASSWORD: {
        return (
          <input
            type="password"
            className="form-control"
            id={id}
            placeholder={placeholder}
            value={value}
            readOnly={readOnly}
            required={required}
            disabled={disabled}
            onChange={this.handleOnChange}
            onBlur={this.handleOnBlur}
            onFocus={this.handleOnFocus}
            autoComplete={this.getAutocompleteStatus()}
          />
        );
      }
      case FormControlType.DATE:
      case FormControlType.DATETIME: {
        return (
          <DateInput
            disabled={disabled}
            size={size}
            value={value}
            locale={locale}
            onChange={this.handleOnChange}
            onFocus={this.handleOnFocus}
            onBlur={this.handleOnBlur}
          />
        );
      }
      case FormControlType.TEL: {
        return (
          <PhoneNumberInput
            locale={locale}
            value={value}
            disabled={disabled}
            required={required}
            size={size}
            searchPlaceholder={searchPlaceholder}
            placeholder={placeholder}
            onChange={this.handleOnChange}
            onFocus={this.handleOnFocus}
            onBlur={this.handleOnBlur}
          />
        );
      }
      case FormControlType.TEXTAREA: {
        return (
          <textarea
            className="form-control"
            id={id}
            name={name}
            placeholder={placeholder}
            value={value}
            readOnly={readOnly}
            required={required}
            // pattern={this.props.pattern}
            minLength={minLength}
            maxLength={maxLength}
            onChange={this.handleOnChange}
            onFocus={this.handleOnFocus}
            onBlur={this.handleOnBlur}
            disabled={disabled}
            autoComplete={this.getAutocompleteStatus()}
          />
        );
      }
      case FormControlType.TEXT:
      default: {
        return (
          <input
            type="text"
            className="form-control"
            id={id}
            name={name}
            placeholder={placeholder}
            value={value}
            readOnly={readOnly}
            required={required}
            // pattern={this.props.pattern}
            minLength={minLength}
            maxLength={maxLength}
            onChange={this.handleOnChange}
            onFocus={this.handleOnFocus}
            onBlur={this.handleOnBlur}
            disabled={disabled}
            // tw-text-format={this.props.textFormat}
            autoComplete={this.getAutocompleteStatus()}
          />
        );
      }
    }
  }
}



